body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.layout {
  width: 890px;
  margin: auto;
}

.navigation {
  background: #e6e6e6;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 0 0 5px 5px;
}

.navigation__link {
  color: #2f2f2f;
  text-decoration: none;

}

.navigation__link:hover {
  color: #000000;
}
.users {
  list-style-type: none;
  padding: 0;
}

.users__item {
  margin: 15px 5px;
}

.users__item-link {
  color: #424242;
  text-decoration: none;
}

.users__item-link:hover {
  color: #000000;
}
.user {
  text-align: center;
}

.user__avatar {
  height: 150px;
  border-radius: 3px;
}

.user__login {
  margin-top: 10px;
  color: #000000;
  font-size: 1.3rem;
}

.user__delete-button {
  background-color: #bd6361;
  border: none;
  color: white;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.user__delete-button:hover {
  background-color: #bd5757;
}

.user__delete-button:disabled {
  background-color: rgba(189, 87, 87, 0.5);
}
.new-user {}

.new-user__title {
  margin: 10px 0;
  color: #000000;
  font-size: 1.3rem;
}

.new-user__label {
  width: 100px;
  display: inline-block;
}

.new-user__create-button {
  margin-top: 20px;
  background-color: #5993de;
  border: none;
  color: white;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.new-user__create-button:hover {
  background-color: #4b87de;
}

.new-user__create-button:disabled {
  background-color: rgba(89, 147, 222, 0.5);
}
